.main-nav {
  line-height: 40px;
  text-align: center;
  text-transform: uppercase;
  padding-left: 0;
  margin: 0;
  right: 0;
  top: 0;
  position: absolute;
  left: 0;
  z-index: 10;
}
.main-nav--nojs {
  position: static;
  order: -1;
  margin: 0 -20px;
}
.main-nav--nojs .main-nav__toggle {
  display: none;
}
.main-nav__list {
  text-decoration: none;
  margin: 0;
  padding: 0;
  display: flex;
  flex-direction: column;
}
.main-nav__toggle {
  position: absolute;
  display: block;
  right: 18px;
  top: 15px;
  border: none;
  background: none;
  width: 23px;
  height: 23px;
  padding: 0;
}
.main-nav__toggle::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 4px;
  background-color: #81b3d2;
  box-shadow: 0 9px 0 0 #81b3d2, 0 18px 0 0 #81b3d2;
}
.main-nav__toggle:active::before {
  background-color: #5496db;
  box-shadow: 0 8px 0 0 #5496db, 0 16px 0 0 #5496db;
}
.main-nav__toggle:hover::before {
  background-color: #669ec0;
  box-shadow: 0 8px 0 0 #669ec0, 0 16px 0 0 #669ec0;
}
.main-nav__opened .main-nav__toggle {
  position: absolute;
  width: 28px;
  height: 28px;
  color: #ffffff;
}
.main-nav__opened .main-nav__toggle::before,
.main-nav__opened .main-nav__toggle::after {
  content: "";
  position: absolute;
  top: 11px;
  left: 0;
  width: 100%;
  height: 4px;
  background-color: #ffffff;
}
.main-nav__opened .main-nav__toggle::before {
  transform: rotate(45deg);
  box-shadow: none;
}
.main-nav__opened .main-nav__toggle::after {
  transform: rotate(-45deg);
  box-shadow: none;
}
.main-nav__opened .main-nav__toggle:active::before,
.main-nav__opened .main-nav__toggle:active::after {
  background-color: #5496db;
  box-shadow: none;
}
.main-nav__opened .main-nav__toggle:hover::before,
.main-nav__opened .main-nav__toggle:hover::after {
  background-color: #669ec0;
  box-shadow: none;
}
@media (min-width: 768px) {
  .main-nav {
    position: static;
    padding: 0;
    margin: 0 -20px;
  }
  .main-nav__list {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    height: 57px;
    align-items: center;
    padding: 0;
  }
  .main-nav__toggle {
    display: none;
  }
}
